@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
/* Global */

* {
  font-family: Avenir, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto,
    Helvetica Neue, Arial, Noto Sans, sans-serif, Apple Color Emoji,
    Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji;
}

/* Chats Page */

.chats-page {
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100vw;
  height: 100vh;
}

.nav-bar {
  width: 100%;
  height: 66px;
  background-color: #002766;
}

.logo-tab {
  position: absolute;
  left: 22px;
  top: 12px;
  font-size: 32px;
  font-weight: 700;
  color: white;
}

.logout-tab {
  position: absolute;
  top: 22px;
  right: 22px;
  color: white;
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 0; /* Remove scrollbar space */
  background: transparent; /* Optional: just make scrollbar invisible */
}

/* Login Page */

#login-page {
  background-image: linear-gradient(#40a9ff, #096dd9);
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
}

#login-card {
  position: relative;
  top: calc(50vh - 144px);
  left: calc(50vw - 210px);
  padding-top: 36px;
  padding-bottom: 66px;
  width: 400px;
  text-align: center;
  background-color: white;
  border-radius: 30px;
}

.login-button {
  cursor: pointer;
  color: white;
  padding: 20px;
  margin-top: 20px;
  border-radius: 8px;
  display: inline-block;
}

.google {
  background-color: #4285f4;
  color: white;
}

#loading-container {
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  background-color: rgb(255, 255, 255, 0.22);
}

#loading-icon {
  font-size: 66px;
  position: relative;
  left: calc(50vw - 33px);
  top: calc(50vh - 22px);
}

* {
  padding: 0;
  margin: 0;
  font-family: "Libre Baskerville", serif;
}
:root {
  --primary-color: #16222a;
  --secondary-color: #8ed1fc;
}
body {
  background-color: rgb(240, 248, 255);
}
html {
  font-size: 75.5%;
}

.header {
  width: 100%;
  height: 600px;
  color: #16222a;
  color: var(--primary-color);
  box-shadow: 0 5px 10px #8ed1fc;
  box-shadow: 0 5px 10px var(--secondary-color);
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
}
.row2 {
  position: absolute;
  top: 10px;
  right: 150px;
  text-align: center;
}
.row2 h2 {
  font-size: 3rem;
  margin-top: 20px;
  margin-bottom: 15px;
  font-family: "Abril Fatface", cursive;
}
.row2 img {
  margin-top: 30px;
  margin-left: 30px;
  width: 645px;
  position: absolute;
  right: 900px;
  top: -35%;
  -webkit-clip-path: circle(100% at left 180px);
          clip-path: circle(100% at left 180px);
  z-index: -1;
}
.row1 h1 {
  font-size: 2.5rem;
  margin-top: 50px;
  text-align: center;
  position: absolute;
  left: 1000px;
  top: 150px;
  margin-left: 35px;
  margin-right: 30px;
  font-family: "Abril Fatface", cursive;
}
.search {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 700px;
  width: 100%;
}
.search input {
  outline: 0;
  border: 0;
  width: 350px;
  height: 35px;
  padding: 0 0.5rem;
  font-size: 15px;
  font-weight: bold;
}
.search button {
  outline: none;
  border: none;
  height: 35px;
  padding: 0 0.5rem;
}
.container {
  width: 70%;
  height: 50%;
  margin: 0, auto;
  margin-top: 30px;
  margin-left: 300px;
  display: grid;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
  grid-template-columns: repeat(auto-fit, minmax(250px, 0.5fr));
  grid: gap 20px;
  box-sizing: border-box;
  padding-right: 20px;
}
.card {
  background-color: #8ed1fc;
  background-color: var(--secondary-color);
  border-radius: 1rem;
  padding: 2rem;
  margin-bottom: 10px;
  box-shadow: 0, 5px 5px rgb(240, 248, 255);
  transition: 0.6s;
  text-align: center;
  position: relative;
}
.card:hover {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  background-color: #8ed1fc;
}
.card img {
  width: 90%;
  height: 200px;
  border-radius: 1rem 1 rem 0 0;
}
.card .bottom {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-bottom: 12px;
}
.card .bottom .title {
  font-size: 14px;
  margin-bottom: 12px;
  font-family: "Abril Fatface", cursive;
}
.card .bottom .amount {
  position: absolute;
  bottom: 10px;
  left: 50px;
  right: 50px;
  background-color: rgb(7, 72, 129);
  color: #16222a;
  color: var(--primary-color);
  font-size: 13px;
  font-weight: bold;
  padding: 0.2rem;
}
.overlay {
  min-height: 300vh 300vh;
  width: 100%;
  position: fixed;
  left: 400px;
  right: =350px;
  bottom: 350px;
  top: 100px;
  dipslay: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.overlay-inner {
  background: white;
  width: 700px;
  height: 550px;
  padding: 1.5rem;
  position: relative;
  box-sizing: border-box;
  overflow: scroll;
  font-size: 1.3rem;
}
.overlay-inner .close {
  position: absolute;
  top: 1rem;
  right: 1rem;
  outline: none;
  border: none;
  font-size: 17px;
  background-color: transparent;
}
.overlay-inner .inner-box {
  margin-top: 30px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
}
.overlay-inner .inner-box img {
  margin-right: 130px;
  width: 200px;
  height: 200px;
}

.overlay-inner .inner-box h3 {
  margin-top: 10px;
  color: green;
}
.overlay-inner .inner-box h4 {
  color: blue;
}
.overlay-inner .inner-box button {
  outline: none;
  border: none;
  padding: 0.8rem 0.5rem;
  width: 80px;
  border-radius: 8px;
  background-color: #24a0ed;
  color: white;
  font-weight: bold;
}
.overlay-inner .description {
  margin-top: 2rem;
  text-align: justify;
  font-size: 13px;
  font-family: "Abril Fatface", cursive;
}

